import React from 'react';
import { GoodsListProps } from '../types';

export const GoodsList: React.FC<GoodsListProps> = ({ goods }) => (
  <>
    {goods && (
      <ul className="container">
        {goods.map(good => (
          <li
            key={good.id}
            className="good level-item"
            style={{ color: `${good.color}` }}
          >
            {good.name}
          </li>
        ))}
      </ul>
    )}
  </>
);
